<template>
  <div>
    <div class="header">
      <div class="left_box">五安能源</div>
      <div class="right_box">
        <div class="right_box_min">
          <el-tabs
            v-model="activeName"
            @tab-click="handleClick"
            tab-position="bottom"
          >
            <el-tab-pane label="商户中心" name="0"></el-tab-pane>
            <el-tab-pane label="运营管理" name="1"></el-tab-pane>
            <el-tab-pane label="硬件管理" name="2"></el-tab-pane>
            <el-tab-pane label="财务管理" name="3"></el-tab-pane>
            <el-tab-pane label="数据报表" name="4"></el-tab-pane>
            <el-tab-pane label="系统设置" name="5"></el-tab-pane>
          </el-tabs>
        </div>

        <div class="right_box_dropdown">
          <el-dropdown>
            <span class="el--link">
              刘慧雨<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeName: "0",
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
  },
};
</script>

<style lang="less" scoped>
.header {
  width: 1920px;
  height: 64px;
  background-color: #f7f7f7;
  border-bottom: 1px solid #e3e6e9;
  box-sizing: border-box;
  .left_box {
    width: 240px;
    height: 64px;
    line-height: 64px;
    text-align: center;
    color: #909399;
    font-family: PingFangSC-Light;
    font-size: 20px;
    float: left;
  }
  .right_box {
    float: left;
    width: calc(100% - 240px);
    height: 64px;
    position: relative;
    .right_box_min {
      position: absolute;
      bottom: 2px;
      left: 64px;
    }
    .right_box_dropdown {
      position: absolute;
      right: 72px;
      height: 64px;
      line-height: 64px;
    }
  }
}
</style>


