import axios from 'axios'

var url = require('./config').default.apiURL;
axios.defaults.baseURL = url


import {
  Message
} from 'element-ui'
axios.interceptors.request.use(function (config) {
  let token = window.localStorage.getItem('userInfo')
  if (!token) {
    window.location.hash = '#/login'
  }
  config.headers.Authorization = `${token}`
  return config
}, function (error) {
  return Promise.reject(error)
})

axios.interceptors.response.use(
  response => {
    if (response.config.responseType === 'blob') {
      // alert('responseblob')
      const fileName = decodeURI(
        // response.headers['content-disposition'] 默认获取不到，需要在 .Net Core Startup.cs 中进行配置
        response.headers['content-disposition'].split('filename*=UTF-8\'\'')[1]
      )
      return Promise.resolve({
        data: response.data,
        fileName: fileName
      })
    } else {
      // alert(response.data)
      return response.data ? response.data : {}
    }
  },
  error => {
    console.log('错误了');
    console.log(error);
    let code = error.response ? error.response.status : ''
    let message = ''
    switch (code) {
      case 400:
        message = '请求参数错误'
        break
      case 403:
        message = '用户非实名认证用户'
        break
      case 507:
        message = '服务器数据库异常'
        break
      case 404:
        message = '手机号码不正确'
        break
      case 401:
        // message = 'token过期或未传'
        // 清空本地
        window.localStorage.clear()
        window.location.hash = '#/login'
        break
      case 500:
        message = '服务器错误500'
        break
      default:
        message = '未知错误!'
        break
    }
    if (code != 401) {
      Message({
        message,
        type: 'warning'
      })
    }


    return new Promise(function () {})
  }
)
export default axios



