<template>
  <div class="bg">
    <!-- 头部 -->
    <layout-header></layout-header>
    <!-- 左侧 -->
    <div class="left">
      <el-menu
        @select="navigationClick"
        :default-active="$router.currentRoute.path"
        :router="true"
        class="el-menu-vertical-demo"
      >
        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-s-grid"></i>
            <span>财务管理</span>
          </template>
          <el-menu-item index="/one">微信商户账户</el-menu-item>
          <el-menu-item index="/two">富有支付</el-menu-item>
        </el-submenu>
      </el-menu>
    </div>

    <!-- 视图中心 -->
    <div class="box_admin">
      <div class="top_box">
        <el-tabs
          tab-position="top"
          :closable="isClosable"
          v-model="activeName"
          type="card"
          @tab-click="tabClick"
          @edit="closeTab"
        >
          <el-tab-pane
            v-for="item in list"
            :key="item.path"
            :label="item.name"
            :name="item.path"
          ></el-tab-pane>
        </el-tabs>
      </div>
      <div class="box_min">
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 导航路由
      navigationList: [
        {
          path: "/one",
          name: "微信商户账户",
        },
        {
          path: "/two",
          name: "富有支付",
        },
      ],
      // 被新增的路由
      list: [
        {
          path: "/one",
          name: "微信商户账户",
        },
      ],
      // 默认展示的路由
      activeName: "/one",
      isClosable: false,
    };
  },
  watch: {
    list: function (newVal, oldVal) {
      let that = this;
      // console.log(newVal)
      // that.isClosable = newVal.length <= 1 ? true : false;
      if (newVal.length <= 1) {
        that.isClosable = false;
      } else {
        that.isClosable = true;
        console.log(that.isClosable);
        console.log(123);
      }
    },
  },
  methods: {
    // tabs标签页点击事件
    tabClick() {
      this.$router.push({ path: this.activeName });
    },
    // 左侧导航栏点击事件
    navigationClick(e) {
      let that = this;
      // 修改tabs activeName的值
      that.activeName = e;
      // 新增数据
      let isOk = that.list.filter((item) => {
        if (item.path == e) {
          return item;
        }
      });
      if (isOk.length != 0) return;

      // list数组没有此数据可以进行push
      let n1 = that.navigationList.filter((item) => {
        if (item.path == e) {
          return item;
        }
      });
      if (n1.length > 0) {
        that.list = that.list.concat(n1);
      } else {
        console.error(
          "配置左侧导航栏时，请前往组件layout/left.vue文件list中配置"
        );
      }
    },
    closeTab(e) {
      console.log(e);
      let that = this;
      // 仅剩一个的时候不允许执行删除操作
      if (that.list.length == 1) {
        that.$message.error("系统不允许删除最后一个tabs");
        return;
      }

      that.list = that.list.filter((item) => {
        if (item.path != e) {
          return item;
        }
      });
      that.activeName = that.list[that.list.length - 1].path;
    },
  },
  created() {
    let that = this;
    that.activeName = that.$router.currentRoute.path;
    let n1 = that.navigationList.filter((item) => {
      if (that.activeName == item.path) {
        return item;
      }
    });
    that.list = n1;
  },
};
</script>

<style lang="less" scoped>
.left {
  background-color: #fff;
  width: 240px;
  // height: calc(100vh - 65px);
  height: calc(100vh - 65px + constant(safe-area-inset-bottom));
  height: calc(100vh - 65px + env(safe-area-inset-bottom));
  border-right: 1px solid #e3e6e9;
  float: left;
}
.bg {
  width: 100%;
  // height: 100vh;
  height: calc(100vh + constant(safe-area-inset-bottom));
  height: calc(100vh + env(safe-area-inset-bottom));
  overflow: hidden;
  background-color: #f6f9fc;
}
.box_admin {
  width: 1632px;
  height: 960px;
  float: left;
  margin: 32px 0 0 24px;
  background-color: #fff;
  position: relative;
  .top_box {
    width: 1632px;
    height: 40px;
  }
  .box_min {
    width: 1632px;
    height: 920px;
    padding: 0 36px;
    box-sizing: border-box;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.03);
  }
}
</style>


